<template>
    <b-row class="justify-content-md-center mt-4">
        <b-col md="7" class="tabs">
            <b-card no-body  bg-variant="dark" text-variant="white">
                <b-tabs content-class="mt-3" pills card fill>
                    <b-tab title="看过的电影" active>
                        <div>
                            <b-card-group columns>
                                <b-card :title="movie.name" img-src="https://placekitten.com/g/400/500" img-alt="Image"
                                        img-top v-for="(movie,index) in movies" :key="index"
                                        bg-variant="dark" text-variant="white">
                                    <b-card-text>
                                        描述：{{movie.desc}}<br/>
                                        评分：{{movie.rank}} ⭐<br/>
                                        短评：{{movie.critique}}
                                    </b-card-text>
                                </b-card>
                            </b-card-group>
                        </div>
                    </b-tab>
                    <b-tab title="看过的书">
                        <div>
                            <b-card-group columns>
                                <b-card :title="book.name" img-src="https://placekitten.com/g/400/500" img-alt="Image"
                                        img-top v-for="(book,index) in books" :key="index"
                                        bg-variant="dark" text-variant="white">
                                    <b-card-text>
                                        作者：{{book.author}}<br/>
                                        评分：{{book.rank}} ⭐<br/>
                                        短评：{{book.critique}}
                                    </b-card-text>
                                </b-card>
                            </b-card-group>
                        </div>
                    </b-tab>
                    <b-tab title="听过的音乐">
                        <div>
                            <b-card-group columns>
                                <b-card :title="music.name" img-src="https://placekitten.com/g/400/500" img-alt="Image"
                                        img-top v-for="(music,index) in musics" :key="index"
                                        bg-variant="dark" text-variant="white">
                                    <b-card-text>
                                        艺术家：{{music.artist}}<br/>
                                        评分：{{music.rank}} ⭐<br/>
                                        短评：{{music.critique}}
                                    </b-card-text>
                                </b-card>
                            </b-card-group>
                        </div>
                    </b-tab>
                </b-tabs>
            </b-card>
        </b-col>
        <b-col md="auto" class="profile">
            <b-card no-body style="max-width: 20rem;" bg-variant="dark" text-variant="white">
                <template v-slot:header>
                    <h4 class="mb-0">{{user.header}}</h4>
                </template>

                <b-card-body>
                    <b-card-img alt="Head" :src="user.head" top=true class="userHead"></b-card-img>
                    <b-card-title :title="user.name"></b-card-title>
                    <b-card-sub-title class="mb-2">💖{{user.heart}}</b-card-sub-title>
                    <b-card-text>
                        {{user.intro}}
                    </b-card-text>
                </b-card-body>

                <b-list-group flush>
                    <b-list-group-item variant="dark">has watched {{user.movie.watched}} movies.</b-list-group-item>
                    <b-list-group-item variant="dark">has read {{user.book.read}} books.</b-list-group-item>
                    <b-list-group-item variant="dark">has heard {{user.music.heard}} albums.</b-list-group-item>
                </b-list-group>

                <b-card-body>
                    <b-button variant="info" class="ml-2">follow</b-button>
                    <b-button variant="success" class="ml-2">message</b-button>
                    <b-button variant="danger" class="ml-2">block</b-button>
                </b-card-body>
                <b-card-footer>aaaaaa</b-card-footer>
            </b-card>
        </b-col>
    </b-row>
</template>

<script>
    export default {
        name: "profile",
        data() {
            return {
                user: {
                },
                movies: [],
                books: [],
                musics: []
            }
        }
    }
</script>

